.ProgressBar {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.ProgressBar > .handle {
  width: 2px;
  background-color: white;
  position: relative;
  left: 0%;
  -webkit-transition: left .2s;
  -o-transition: left .2s;
  transition: left .2s;
  opacity: 0.8;
}

.ProgressBar > .point {
  position: absolute;
  background-color: white;
  opacity: 0.5;
  width: 4px;
  height: 4px;
  border-radius: 2px;
}



